//Production
export const URL = "https://backendserale.onrender.com"

setBasePath('https://cdn.jsdelivr.net/npm/@shoelace-style/shoelace@2.15.0/cdn/');

function App() {

  //Move Below to App get username only when authentication is valud. It can be located in Shell too
  //so it forces the authentication.

  const [user, setUser] = useState({
    username: "",
    propertymanager: false
  })


  console.log("User Info at Login")
  console.log(user)
  return (

    <BrowserRouter>
      <Routes>
        <Route path="/" element={<Welcome />} />
        <Route path="/propertymanagement" element={<ProtectedRoute><Shell user={user}/></ProtectedRoute>} />
        <Route path="/propertydevelopment" element={<Development />} />
        <Route path="*" element={<Navigate to="/" />} />
        <Route path="/login" element={<Login setUser={setUser}/>} />
      </Routes>
    </BrowserRouter>
  );
}

export default App;
